import React from 'react';
import { motion } from 'framer-motion';
import { cn } from '../utils';

/**
 * Card Component
 * Thẻ hiển thị nội dung với animation
 */
export const Card = ({
  children,
  className = '',
  onClick,
  hover = true,
  ...props
}) => {
  return (
    <motion.div
      className={cn(
        'bg-white rounded-2xl shadow-lg p-6 border border-gray-100',
        'backdrop-blur-sm bg-white/95',
        onClick && 'cursor-pointer transition-all duration-300',
        className
      )}
      onClick={onClick}
      whileHover={
        hover && onClick
          ? { scale: 1.02, y: -4, boxShadow: '0px 12px 30px rgba(15,23,42,0.12)' }
          : {}
      }
      whileTap={onClick ? { scale: 0.98 } : {}}
      initial={{ opacity: 0, y: 20, boxShadow: '0px 6px 18px rgba(15,23,42,0.06)' }}
      animate={{ opacity: 1, y: 0, boxShadow: '0px 10px 24px rgba(15,23,42,0.08)' }}
      transition={{ duration: 0.3 }}
      {...props}
    >
      {children}
    </motion.div>
  );
};

